@import url('./switcher.css');
@import url('./menu_button.css');

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  -o-box-sizing: border-box;
  box-sizing: border-box;
}

header {
  height: 100px;
}

.cardWrapper {
  width: 18.75rem;
  height: 17.5rem;
  margin: 10px 10px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  font-size: 24px;
  line-height: 36px;
  font-weight: 400;
  color: #212529;
  border: 1px solid #b7b3b3;
  border-radius: 5px;
  transform-style: preserve-3d;
  transition: all 0.5s ease-out;
}

.cardWrapperMain {
  background: linear-gradient(to bottom, #285977 45%, transparent 50%);
}

.cardWrapperMainPlay {
  background: linear-gradient(to bottom, #e2a41d 45%, transparent 50%);
}

.cardWrapperMainEl {
  height: 60% !important;
  border-radius: 50% !important;
  width: 60%;
  margin: 9% auto;
  background-size: 150%;
}

.transform {
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
  transition: all 0.5s ease-out;
}

.transformNow {
  -webkit-transform: rotateY(180deg);
  transform: rotateY(180deg);
}

.hidden {
  display: none !important;
}

.visible {
  display: block;
}

.cardWrapper:hover {
  box-shadow: 0 10px 20px rgba(0,0,0,.4);
  cursor: pointer;
}

.cardWrapper__element {
  height: 85%;
  background-position: center;
  border-radius: 5px;
}

.cardWrapper__elementPlay {
  height: 100%;
  background-size: cover;
}

.cardWrapper__title {
  display: flex;
  justify-content: center;
  position: relative;
}

.cardWrapper__roundCard {
  position: absolute;
  right: 1rem;
}

.cardWrapper__roundCard:hover {
  -webkit-filter: sepia();
}

.container,
.container_categories {
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  flex-wrap: wrap;
}

.wrapper {
  position: relative;
  overflow-x: hidden;
}

.menu {
  position: fixed;
  left: 0;top: 0;
  z-index: 99;
  width: 35%;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  transition: 0.5s;
  transform: translateX(-100%);
  background: linear-gradient(40deg,#346975,#11c7df);
}

.menu_active {
  transform: translateX(0%);
}

.menu-list {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 50%;
  flex-direction: column;

}

.menu-list a {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  font-size: 22px;
  color: white;
  text-decoration: none;
  font-weight: 900;
}

.menu-btn {
  position: absolute;
  right: -51px;
  top: 17px;
  border-radius: 2px;
}

.content {
  transition: 0.5s;
  position: relative;
  z-index: 0;
  margin-bottom: 2rem;
  display: flex;
  flex-flow: wrap;
}

.content_active {
  transform: translateX(30%);
}

.playButton {
  margin-top: 1rem;
  width: 10rem;
  background-color: #ffba37;
  display: flex;
  margin: 0 auto;
  height: 40px;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  font-size: initial;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

.footer {
  display: flex;
  margin-top: 2rem;
  justify-content: space-around;
  align-items: center;
}

.image-footer {
  height: 5rem;
}

footer p {
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
}

.starImage {
  margin: 0px 87%;
}

@media (max-width: 470px) {
  .menu-list a {
    font-size: 14px;
  }
}
